import Vue from 'vue'
import Vuex from 'vuex'
import VuexPersistence from 'vuex-persist'

const vuexLocal = new VuexPersistence({
  storage: window.localStorage
})

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    shoppingCart: [],
    notification: ''
  },
  mutations: {
    add(state, payload) {
      const isAlreadyInTheCart = state.shoppingCart.filter((shoppingCartObj) => {
        return shoppingCartObj.product.id === payload.product.id;
      })
      if (isAlreadyInTheCart.length === 0) {
        state.shoppingCart.push(payload);
        state.notification = 'Product added to the cart';
      } else {
        state.notification = 'Product already in the cart';
      }
    },
    remove(state, payload) {
      state.shoppingCart = state.shoppingCart.filter((shoppingCartObj) => {
        return shoppingCartObj.product.id !== payload.product.id;
      });
    },
    removeAll(state, payload) {
      state.shoppingCart = [];
    },
    update(state, payload) {
      state.shoppingCart.forEach((obj) => {
        if (obj.product.id === payload.product.id) {
          obj.quantity = payload.quantity;
        }
      });
    }
  },
  actions: {
    add(state, payload) {
      state.commit('add', payload);
    },
    remove(state, payload) {
      state.commit('remove', payload);
    },
    removeAll(state, payload) {
      state.commit('removeAll', payload);
    },
    update(state, payload) {
      state.commit('update', payload);
    }
  },
  getters: {
    shoppingCart(state) {
      return state.shoppingCart;
    },
    notification(state) {
      return state.notification;
    },
    shoppingCartTotal(state) {
      if (state.shoppingCart.length !== 0) {
        let shoppingCartTotal = 0;
        state.shoppingCart.forEach((shoppingCartObj) => {
          shoppingCartTotal += shoppingCartObj.product.price * shoppingCartObj.quantity;
        });
        return shoppingCartTotal.toFixed(2);
      } else {
        return 0;
      }
    }
  },
  plugins: [vuexLocal.plugin]
})
