<template>
  <header class="header" :class="headerClass" ref="header">
    <div class="header-main">
      <div class="container">
        <!-- Logo -->
        <router-link to="/" tag="a" class="header__logo">
          <img src="/img/icons/logo.svg" alt="logo-img">
          <p>T-Shop</p>
        </router-link>
        <!-- Shop Link -->
        <router-link to="/shop" tag="a" class="header__shop-link ml-auto">
          Shop
        </router-link>
        <!-- Cart -->
        <router-link to="/cart" tag="a" class="header__cart-link">
          <span class="header__icon header__icon--cart">
            <span class="shopping-cart__number">{{ shoppingCartNumber }}</span>
          </span>
        </router-link>
      </div>
    </div>
  </header>
</template>

<script>
  export default {
    props: ['headerClass'],
    methods: {
      animateHeaderOnScroll() {
        const header = this.$refs.header;

        window.addEventListener('scroll', () => {
          if (window.scrollY > 0) {
            header.classList.remove('header--transparent');
            header.classList.add('header--white');
          } else {
            header.classList.add('header--transparent');
            header.classList.remove('header--white');
          }
        });
        if (window.scrollY > 0) {
          header.classList.remove('header--transparent');
          header.classList.add('header--white');
        } else {
          header.classList.add('header--transparent');
          header.classList.remove('header--white');
        }
      },

      addBodyPadding() {
        const header = this.$refs.header;
        const headerHeight = header.offsetHeight;
        const body = document.querySelector('body');

        body.style.paddingTop = headerHeight + 'px';
      }
    },
    computed: {
      shoppingCartNumber() {
        return this.$store.getters.shoppingCart.length;
      }
    },
    mounted() {
      if (this.headerClass === 'header--transparent') {
        this.animateHeaderOnScroll();
        const body = document.querySelector('body');
        body.style.paddingTop = 0;
      } else {
        this.addBodyPadding();
      }
    }
  }
</script>

<style lang="scss" scoped>
  .header--transparent .header__icon--cart {
    background-image: url("/img/icons/cart-white.svg")
  }

  .header--white .header__icon--cart {
    background-image: url("/img/icons/cart-black.svg")
  }
</style>