<template>
  <router-link :to="`/product/${product.id}`" tag="a" class="product" :class="sliderClass">
    <figure class="product__img">
      <img :src="product.images[0]" alt="product-img">
    </figure>
    <div class="product__info">
      <h3 class="product__title">{{ product.name }}</h3>
      <p class="product__price product__price--sale">
        ${{ product.price }}
      </p>
    </div>
  </router-link>
</template>

<script>
  export default {
    props: ['product', 'sliderClass']
  }
</script>