<template>
  <div class="product-slider__wrapper">
    <div class="container">
      <!-- Title -->
      <slot></slot>
      <!-- Product Slider -->
      <div class="swiper-container product-slider" ref="productSlider">
        <!-- Products Wrapper -->
        <div class="swiper-wrapper">
          <!-- Swiper Slide / Product -->
          <app-product 
            v-for="product in products"
            :key="product.id"
            :product="product"
            sliderClass="swiper-slide"
          >
          </app-product>
        </div>
      </div>
    </div>
    <!-- Navigation Buttons -->
    <div class="swiper-navigation-btns">
      <div class="container">
        <div class="swiper-button-prev"></div>
        <div class="swiper-button-next"></div>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios';
  import Swiper from 'swiper';
  import Product from '../components/Product.vue';

  export default {
    data() {
      return {
        products: []
      };
    },
    components: {
      'app-product': Product
    },
    methods: {
      loadProducts() {
        if (this.products.length === 0) {
          axios.get('products.json')
            .then((response) => {
              this.products = response.data.products;
            }).then(() => {
              this.initProductSlider();
            });
        }
      },
      initProductSlider() {
        const productSlider = this.$refs.productSlider;
        if (productSlider) {
          new Swiper(productSlider, {
            slidesPerView: 4,
            spaceBetween: 40,
            loop: true,

            autoplay: {
              delay: 5000,
              disableOnInteraction: true,
            },

            breakpoints: {
              0: {
                slidesPerView: 2,
                autoplay: false,
                spaceBetween: 15
              },
              767: {
                slidesPerView: 3,
                autoplay: false,
                spaceBetween: 30
              },
              991: {
                autoplay: true
              },
            },

            navigation: {
              nextEl: '.swiper-button-next',
              prevEl: '.swiper-button-prev',
            },
          });
        }
      }
    },
    mounted() {
      // this.initProductSlider();
      this.loadProducts();
    }
  }
</script>

<style lang="scss" scoped>
  .product-slider__wrapper .swiper-button-prev::after {
    background-image: url("/img/icons/arrow-black.svg");
  }

  .product-slider__wrapper .swiper-button-next::after {
    background-image: url("/img/icons/arrow-black.svg");
  }
</style>